<template>
 <div id="desktop-register">
    <v-main>
      <div style="font-family:'bgtitlefamily';font-size:48px;color:white;margin:15% 12%;">
        <img src="@/assets/app/logo.png" alt="" style="width:50px;position:relative;top:10px;">
        问卷系统
      </div>
      <div class="area-input">
        <div class="head">
          <span id="rec-grey"></span>
          <span id="rec-white"></span>
          <span id="title-words">
            <div>找回密码</div>
            <div>FindPass</div>
          </span>
        </div>
        <div class="input-body">
          <v-form ref="userform" :rules="rules" :model="userform">
            <v-container>
              <v-row>
                <v-text-field
                    class="styled-text-field"
                    label="手机号"
                    placeholder="手机号"
                    outlined
                    dense
                    v-model="userform.phone"
                    validate-on-blur
                    :rules="[validateUsername]"
                ></v-text-field>
              </v-row>
              <v-row>
                <v-text-field
                    class="styled-text-field"
                    label="短信验证码"
                    placeholder="短信验证码"
                    outlined
                    dense
                    validate-on-blur
                    :rules="[validateCheckCode]"
                    v-model="userform.checkCode"
                >
                  <template v-slot:append>
                    <div
                        @click="send_verification_code"
                        style="cursor:pointer;margin-top:-2px"
                    >
                      <v-icon color="black">mdi-send</v-icon>
                      <v-btn text>
                        发送
                      </v-btn>
                    </div>
                  </template>
                </v-text-field>
              </v-row >
              <v-row>
                <v-text-field
                    class="styled-text-field"
                    label="密码"
                    placeholder="密码"
                    outlined
                    dense
                    :type="show1 ? 'text' : 'password'"
                    @click:append="show1 = !show1"
                    :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                    v-model="userform.password"
                    validate-on-blur
                    :rules="[validatePass1]"
                ></v-text-field>
              </v-row>
              <v-row>
                <v-text-field
                    class="styled-text-field"
                    label="确认密码"
                    placeholder="确认密码"
                    :type="show2 ? 'text' : 'password'"
                    @click:append="show2 = !show2"
                    :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
                    outlined
                    dense
                    v-model="userform.checkPass"
                    validate-on-blur
                    :rules="[validatePass2]"
                ></v-text-field>
              </v-row>

              <v-row justify="space-between">
                <v-btn
                    elevation="2"
                    style="margin-right:7vw;"
                    outlined
                    width="100px"
                    height="40px"
                    @click="to_login"
                >登&nbsp;&nbsp;录</v-btn>
                  <v-btn
                      elevation="2"
                      outlined
                      v-if="this.ispasswordCorrect==true&&this.isphoneCorrect==true&&this.ischeckCodeCorrect==true"
                      width="100px"
                      height="40px"
                      @click="findPass"
                  >确&nbsp;&nbsp;认</v-btn>
                    <v-btn
                      elevation="2"
                      outlined
                      v-else
                      disabled
                      width="100px"
                      height="40px"
                      color="primary"
                      @click="findPass"
                  >确认</v-btn>
              </v-row>

            </v-container>
          </v-form>
        </div>
      </div>
    </v-main>
    </div>
</template>
<script>
import axios from "axios"
export default {
    name:"RegisterCommon",
    components:{
    },
  data(){
    return {
      ischeckCodeCorrect:false,
      isphoneCorrect:false,
      ispasswordCorrect:false,
      show1:false,
      show2:false,
      userform:{
        phone:'',
        checkCode:'',
        password:'',
        checkPass:'',
      },
      rules:{
        phone: [
          {
            required: true,
            validator: this.validateUsername,
            message: "请输入您的电话",
            trigger: "blur",
          },
        ],
        checkCode:[
          { required: true, validator: this.validateCheckCode, trigger: 'blur' }
        ],
        pass: [
          { required: true, validator: this.validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { required: true, validator: this.validatePass2, trigger: 'blur' }
        ],
      }
    }
  },
    created(){
        return ;
    },
    mounted(){
        // return ;
        this.init();
    },
    computed:{

    },

  methods:{
    validateCheckCode(value){
      if(value=='') return '请输入验证码'
          else {
            this.ischeckCodeCorrect=true
            return true
          }
    },
     validateUsername(value) {
      var pattern1 = /^1[3456789]\d{9}$/;
      var res1 = pattern1.test(value);
      if ((res1) === false) {
        return "电话不符合格式";
      } else {
        this.isphoneCorrect=true
        return true;
      }
    },
    validatePass1(value){
      if(value===''){
        return  '请输入密码';
      }else  if (value !== ''){
        return true
      }
    },
      validatePass2( value) {
      if (value === '') {
        return '请再次输入密码';
      } else if (value !== this.userform.password) {
        return  '两次输入密码不一致!';
      } else {
        this.ispasswordCorrect=true
        return
      }
    },
      to_login(){
        this.$router.replace("/login")
      },
      findPass(){
         axios({url:'/findPass',method:'post',data:this.userform}).then((res)=>{
          if(res.data.status === true){
            this.$message.success("找回成功");
              this.$router.push("/login");
          }else {
            this.$message.error({ type: "error", message: res.data.exec});
          }
        }).catch(err =>{
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        })
      },
    send_verification_code(){
      console.log(this.userform);
        axios({url:'/sendCode/findPass',method:'post',data:{phone:this.userform.phone}}).then((res)=>{
          if(res.data.status==true) this.$message.success("发送成功");
          else this.$message.error({ type: "error", message: res.data.exec });
        }).catch(err =>{
          console.log(err);
          this.$message.error({ type: "error", message: "网络错误" });
        })
      },
  }
}
</script>
</script>
<style scoped>
.head {
  margin-top:40px;
  margin-left: 40px;
  width: 100%;
  color: #585858;
  font-size: 22px;
  font-weight: 600;
}
#rec-grey {
  width: 20px;
  background: #585858;
  height: 60px;
  float: left;
}
#rec-white {
  width: 20px;
  height: 60px;
  float: left;
}
#title-words{
  font-size:22px;
  align-content: center;
}

.input-body{
  margin-top: 30px;
  margin-left: 40px;
  margin-right: 30px;
  /*background-color: blue;*/
}

.styled-text-field{
  height: 60px;
  /*font-size:20pt;*/
}
#desktop-register {
  width: 100%;
  height: 100%;
  background-image: url("../../assets/login/bg.jpg");
  background-size: 100% 100%;
}
.area-input {
  position: fixed;
  background-color: rgba(255,251,240,0.7);
  float: right;
  right: 0;
  padding-top: 40px;
  bottom: 0;
  height: 100%;
  width: 25%;
  /*border:10px;*/
}

</style>